// xtx-message
import { createVNode, render } from 'vue'
import XtxMessage from './index.vue'

const div = document.createElement('div')
document.body.appendChild(div)
// 
let timer = null

export default ({ type, text }) => {
  const VNode = createVNode(XtxMessage, { type, text })
  render(VNode, div)
  clearTimeout(timer)
  timer = setTimeout(() => {
    render(null, div)
    // console.log('')
  }, 3000)
}
