import request from '@/utils/request'

/**
 * 
 * @param {Array<object>} cartList - 
 * @param {String} item.skuId - skuId
 * @param {Boolean} item.selected - 
 * @param {Integer} item.count - 
 */
export const mergeLocalCart = (cartList) => {
  return request('/member/cart/merge', 'post', cartList)
}

/**
 * 
 * @returns Promise
 */
export const findCartList = () => {
  return request('/member/cart', 'get')
}

/**
 * 
 * @param {String} skuId - SKUID
 * @param {Integer} count - 
 * @returns Promise
 */
export const insertCart = ({ skuId, count }) => {
  return request('/member/cart', 'post', { skuId, count })
}

/**
 * 
 * @param {Array<string>} ids - skuId
 * @returns Promise
 */
export const deleteCart = (ids) => {
  return request('/member/cart', 'delete', { ids })
}

/**
 * 
 * @param {String} goods.skuId - sku
 * @param {Boolean} goods.selected - 
 * @param {Integer} goods.count - 
 * @returns Promise
 */
export const updateCart = (goods) => {
  return request('/member/cart/' + goods.skuId, 'put', goods)
}

/**
 * 
 * @param {Boolean} selected - 
 * @param {Array<string>} ids - skuId
 * @returns Promise
 */
export const checkAllCart = ({ selected, ids }) => {
  return request('/member/cart/selected', 'put', { selected, ids })
}
