import { findSubCategoryGoods } from '@/api/category'
import { onMounted, reactive, ref } from 'vue'
import { useRoute } from 'vue-router'
// 
// 
export function useGoods () {
  const route = useRoute()
  const reqParams = reactive({
    page: 1,
    pageSize: 20,
    categoryId: route.params.id,
    sortField: null, // 
    attrs: [], // 
    brandId: null // 
  })
  const goodsFilterList = ref([])
  const sortType = ref(null)
  async function getFilterGoods () {
    const res = await findSubCategoryGoods(reqParams)
    goodsFilterList.value = res.result.items
    // OK isLoadingfalse 
    isLoading.value = false
  }
  onMounted(() => {
    getFilterGoods()
  })
  // ,
  function changeSort (type) {
    sortType.value = type
    reqParams.sortField = type
    getFilterGoods()
  }
  // 
  function changeBrand (id) {
    reqParams.brandId = id
    getFilterGoods()
  }

  // 
  function toggleSelected (i, item) {
    //  false true
    item.properties.forEach(item => {
      item.selected = false
    })
    // true
    i.selected = true

    const index = reqParams.attrs.findIndex(attr => attr.groupName === item.name)
    if (index > -1) {
      reqParams.attrs.splice(index, 1)
    }
    reqParams.attrs.push({
      groupName: item.name,
      propertyName: i.name
    })

    //  
    if (i.id === null) {
      const index = reqParams.attrs.findIndex(attr => attr.groupName === item.name)
      if (index > -1) {
        reqParams.attrs.splice(index, 1)
      }
    }
    getFilterGoods()
  }

  // 
  const isLoading = ref(true) // 
  const isFinished = ref(false) // 
  async function loadNext () {
    isLoading.value = true
    reqParams.page++
    const res = await findSubCategoryGoods(reqParams)
    // 
    if (res.result.items.length === 0) {
      isFinished.value = true
    }
    goodsFilterList.value = [...goodsFilterList.value, ...res.result.items]
    isLoading.value = false
  }
  return {
    reqParams,
    goodsFilterList,
    sortType,
    changeSort,
    changeBrand,
    toggleSelected,
    loadNext,
    isLoading,
    isFinished
  }
}
