import request from '@/utils/request'

/**
 * 
 * @param {String} account - 
 * @param {String} password - 
 * @returns Promise
 */

export const userAccountLogin = ({ account, password }) => {
  return request('/login', 'post', { account, password })
}

/**
 * 
 * @param {String} unionId -  qq openId
 * @param {Integer} source -  1pc2webapp3, 4Android, 5ios, 6qq, 7
 * @returns Promise
 */

export const userQQLogin = (unionId, source = 6) => {
  return request('https://pcapi-xiaotuxian-front-devtest.itheima.net/login/social', 'post', { unionId, source })
}

/**
 * QQ
 * @param {String} mobile - 
 * @returns promise
 */
export const userQQBindCode = (mobile) => {
  return request(
    '/login/social/code',
    'get',
    { mobile }
  )
}

/**
 * QQ-
 * @param {String} unionId - QQopenId
 * @param {String} mobile - 
 * @param {String} code - 
 * @returns
 */
export const userQQBindLogin = ({ unionId, mobile, code }) => {
  return request('/login/social/bind', 'post', { unionId, mobile, code })
}

/**
 * QQ
 * @param {String} mobile - 
 * @returns promise
 */
export const userQQPatchCode = (mobile) => {
  return request('/register/code', 'get', { mobile })
}

/**
 * QQ-
 * @param {String} unionId - QQopenId
 * @param {String} mobile - 
 * @param {String} code - 
 * @param {String} account - 
 * @param {String} password - 
 * @returns
 */
export const userQQPatchLogin = ({ unionId, mobile, code, account, password }) => {
  return request(`/login/social/${unionId}/complement`, 'post', { unionId, mobile, code, account, password })
}
