import { findHeadCategory } from '@/api/home'
export default {
  namespaced: true,
  state: () => {
    return {
      list: [] // 
    }
  },
  // listmutations
  mutations: {
    setCategory (state, headCategory) {
      state.list = headCategory
    }
  },
  // actions
  actions: {
    async asyncSetList (ctx) {
      // 1.
      // 2.setList mutation
      const res = await findHeadCategory()

      ctx.commit('setCategory', res.result)
    }
  }
}
