<template>
  <div class="xtx-form">
    <div class="user-info">
      <img :src="qqInfo.avatar" alt="" />
      <p>
        Hi{{ qqInfo.nickname }} QQ~
      </p>
    </div>
    <Form :validation-schema="mySchema" v-slot="{ errors }" autocomplete="off" ref="formRef">
      <div class="xtx-form-item">
        <div class="field">
          <i class="icon iconfont icon-phone"></i>
          <Field
            :class="{ err: errors.mobile }"
            v-model="form.mobile"
            name="mobile"
            class="input"
            type="text"
            placeholder=""
          />
          <!-- <input class="input" type="text" placeholder="" /> -->
        </div>
        <div class="error" v-if="errors.mobile">{{ errors.mobile }}</div>
      </div>
      <div class="xtx-form-item">
        <div class="field">
          <i class="icon iconfont icon-code"></i>
          <Field
            :class="{ err: errors.code }"
            v-model="form.code"
            name="code"
            class="input"
            type="text"
            placeholder=""
          />
          <!-- <input class="input" type="text" placeholder="" /> -->
          <span class="code" @click="send">{{
            curTime === 0 ? "" : curTime + "s"
          }}</span>
        </div>
        <div class="error" v-if="errors.code">{{ errors.code }}</div>
      </div>
    </Form>
    <a href="javascript:;" class="submit" @click="bind"></a>
  </div>
</template>

<script>
import QC from 'qc'
import { reactive, ref } from 'vue'
import { Form, Field } from 'vee-validate'
import schema from '@/utils/vee-validate-schema'
import { useTime } from '@/compositions'
import { userQQBindCode, userQQBindLogin } from '@/api/user'
import { useStore } from 'vuex'
import { useRouter } from 'vue-router'
import message from '@/components/Message/index.js'
export default {
  name: 'CallbackBind',
  props: {
    unionId: {
      type: String
    }
  },
  components: {
    Form, Field
  },
  setup (props) {
    const qqInfo = reactive({
      nickname: '',
      avatar: ''
    })
    // qq 
    // QC.api('get_user_info').success(( res )=>{})  
    // check
    if (QC.Login.check()) {
      QC.api('get_user_info').success((res) => {
        // res:qq
        qqInfo.nickname = res.data.nickname
        qqInfo.avatar = res.data.figureurl_qq_2
      })
    }
    // 
    //  shecma
    const mySchema = {
      mobile: schema.mobile,
      code: schema.code
    }
    // 
    const form = reactive({
      mobile: '',
      code: ''
    })

    // 
    // ``  ``  60
    // 0  0  ``
    const { curTime, start } = useTime(60)
    // 
    // 1.ok
    // 2.
    function send () {
      if (schema.mobile(form.mobile) === true) {
        // curTime0
        if (curTime.value === 0) {
          // 
          userQQBindCode(form.mobile).then(res => {
            start() // 
          })
        }
      }
    }
    // 
    const formRef = ref(null)
    const store = useStore()
    const router = useRouter()
    async function bind () {
      // 
      const res = await formRef.value.validate()
      if (res) {
        userQQBindLogin({ unionId: props.unionId, mobile: form.mobile, code: form.code }).then(res => {
          console.log(res)
          // 1.mutationvuex
          store.commit('user/setUser', res.result)
          // 
          // 2.
          router.push('/')
          // 3.
          message({ type: 'success', text: '' })
        })
      }
    }
    return {
      qqInfo,
      mySchema,
      form,
      curTime,
      send,
      formRef,
      bind
    }
  }
}
</script>

<style scoped lang='less'>
.user-info {
  width: 320px;
  height: 70px;
  margin: 0 auto;
  display: flex;
  background: #f2f2f2;
  align-items: center;
  padding: 0 10px;
  margin-bottom: 25px;
  img {
    background: #f2f2f2;
    width: 50px;
    height: 50px;
  }
  p {
    padding-left: 10px;
  }
}
.code {
  position: absolute;
  right: 0;
  top: 0;
  line-height: 50px;
  width: 80px;
  color: #999;
  &:hover {
    cursor: pointer;
  }
}
</style>
