<template>
  <div class="xtx-pay-page">
    <div class="container">
      <XtxBread>
        <XtxBreadItem to="/"></XtxBreadItem>
        <XtxBreadItem to="/cart"></XtxBreadItem>
        <XtxBreadItem></XtxBreadItem>
      </XtxBread>
      <!--  -->
      <div class="pay-info" v-if="orderInfo.payMoney">
        <span class="icon iconfont icon-queren2"></span>
        <div class="tip">
          <p></p>
          <p> <span>{{countTimeText}}</span>, </p>
        </div>
        <div class="amount">
          <span></span>
          <span>{{orderInfo.payMoney.toFixed(2)}}</span>
        </div>
      </div>
      <!--  -->
      <div class="pay-type">
        <p class="head"></p>
        <div class="item">
          <p></p>
          <a class="btn wx" href="javascript:;"></a>
          <a class="btn alipay" :href="payUrl"></a>
        </div>
        <div class="item">
          <p></p>
          <a class="btn" href="javascript:;"></a>
          <a class="btn" href="javascript:;"></a>
          <a class="btn" href="javascript:;"></a>
          <a class="btn" href="javascript:;"></a>
          <a class="btn" href="javascript:;"></a>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { findOrder } from '@/api/pay'
import { useRoute } from 'vue-router'
import { onMounted, ref } from 'vue'
import { useTime } from '@/compositions'
export default {
  name: 'XtxPayPage',
  setup () {
    const route = useRoute()
    const orderInfo = ref({})
    const { countTimeText, start } = useTime()
    async function getOrderInfo () {
      const res = await findOrder(route.query.orderId)
      orderInfo.value = res.result
      // 
      start(orderInfo.value.countdown)
    }
    onMounted(() => {
      getOrderInfo()
    })

    // 
    //   orderId  
    const baseURL = 'http://pcapi-xiaotuxian-front-devtest.itheima.net/'
    const redirectUrl = encodeURIComponent('http://www.corho.com:8080/#/pay/callback')
    const payUrl = `${baseURL}pay/aliPay?orderId=${route.query.orderId}&redirect=${redirectUrl}`

    return {
      orderInfo,
      getOrderInfo,
      countTimeText,
      payUrl
    }
  }
}
</script>
<style scoped lang="less">
.pay-info {
  background: #fff;
  display: flex;
  align-items: center;
  height: 240px;
  padding: 0 80px;
  .icon {
    font-size: 80px;
    color: #1dc779;
  }
  .tip {
    padding-left: 10px;
    flex: 1;
    p {
      &:first-child {
        font-size: 20px;
        margin-bottom: 5px;
      }
      &:last-child {
        color: #999;
        font-size: 16px;
      }
    }
  }
  .amount {
    span {
      &:first-child {
        font-size: 16px;
        color: #999;
      }
      &:last-child {
        color: @priceColor;
        font-size: 20px;
      }
    }
  }
}
.pay-type {
  margin-top: 20px;
  background-color: #fff;
  padding-bottom: 70px;
  p {
    line-height: 70px;
    height: 70px;
    padding-left: 30px;
    font-size: 16px;
    &.head {
      border-bottom: 1px solid #f5f5f5;
    }
  }
  .btn {
    width: 150px;
    height: 50px;
    border: 1px solid #e4e4e4;
    text-align: center;
    line-height: 48px;
    margin-left: 30px;
    color: #666666;
    display: inline-block;
    &.active,
    &:hover {
      border-color: @xtxColor;
    }
    &.alipay {
      background: url(https://cdn.cnbj1.fds.api.mi-img.com/mi-mall/7b6b02396368c9314528c0bbd85a2e06.png) no-repeat center / contain;
    }
    &.wx {
      background: url(https://cdn.cnbj1.fds.api.mi-img.com/mi-mall/c66f98cff8649bd5ba722c2e8067c6ca.jpg) no-repeat center / contain;
    }
  }
}
</style>
