<template>
  <div class="xtx-pay-checkout-page">
    <div class="container">
      <XtxBread>
        <XtxBreadItem to="/"></XtxBreadItem>
        <XtxBreadItem to="/cart"></XtxBreadItem>
        <XtxBreadItem ></XtxBreadItem>
      </XtxBread>
      <div class="wrapper">
        <!--  -->
        <h3 class="box-title"></h3>
        <div class="box-body">
          <div class="address">
            <div class="text">
              <!-- <div class="none"></div> -->
              <ul>
                <li><span><i/><i/></span>{{address.receiver}}</li>
                <li><span></span>{{address.contact}}</li>
                <li><span></span>{{address.fullLocation}}{{address.address}}</li>
              </ul>
            </div>
            <div class="action">
              <XtxButton class="btn" @click="changeVisible=true"></XtxButton>
              <XtxButton class="btn" @click="addVisible=true"></XtxButton>
            </div>
          </div>
        </div>
        <!--  -->
        <h3 class="box-title"></h3>
        <div class="box-body">
          <table class="goods">
            <thead>
              <tr>
                <th width="520"></th>
                <th width="170"></th>
                <th width="170"></th>
                <th width="170"></th>
                <th width="170"></th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="i in orderInfo.goods" :key="i.id">
                <td>
                  <a href="javascript:;" class="info">
                    <img :src="i.picture" alt="">
                    <div class="right">
                      <p>{{i.name}}</p>
                      <p>{{i.attrsText}}</p>
                    </div>
                  </a>
                </td>
                <td>&yen;{{i.price}}</td>
                <td>{{i.count}}</td>
                <td>&yen;{{i.totalPrice}}</td>
                <td>&yen;{{i.totalPayPrice}}</td>
              </tr>
            </tbody>
          </table>
        </div>
        <!--  -->
        <h3 class="box-title"></h3>
        <div class="box-body">
          <a class="my-btn active" href="javascript:;"></a>
          <a class="my-btn" href="javascript:;"></a>
          <a class="my-btn" href="javascript:;"></a>
        </div>
        <!--  -->
         <h3 class="box-title"></h3>
        <div class="box-body">
          <a class="my-btn active" href="javascript:;"></a>
          <a class="my-btn" href="javascript:;"></a>
          <span style="color:#999">5</span>
        </div>
        <!--  -->
        <h3 class="box-title"></h3>
        <div class="box-body" v-if="orderInfo.summary">
          <div class="total">
            <dl><dt></dt><dd>{{orderInfo.summary.goodsCount}}</dd></dl>
            <dl><dt></dt><dd>{{orderInfo.summary.totalPrice}}</dd></dl>
            <dl><dt><i></i></dt><dd>{{orderInfo.summary.postFee}}</dd></dl>
            <dl><dt></dt><dd class="price">{{orderInfo.summary.totalPayPrice}}</dd></dl>
          </div>
        </div>
        <!--  -->
        <div class="submit">
          <XtxButton type="primary" @click="submitOrder"></XtxButton>
        </div>
      </div>
    </div>
  </div>
  <!--  -->
  <XtxDialog title="" v-model:visible="changeVisible">
      <template #default>
        <div class="addressWrapper">
          <div
          class="text item"
          v-for="item in orderInfo.userAddresses" :key="item.id"
          @click="changeAddress(item)"
          :class="{active: item.id===curAddress.id}"
          >
            <ul>
              <li>
                <span><i /><i /></span>{{ item.receiver }}
              </li>
              <li><span></span>{{ item.contact }}</li>
              <li>
                <span></span>{{ item.fullLocation + item.address }}
              </li>
            </ul>
          </div>
        </div>
      </template>
      <template #footer>
        <div class="footer">
          <XtxButton type="gray" style="margin-right: 20px" @click="changeVisible = false"></XtxButton>
          <XtxButton type="primary" @click="submitChange"></XtxButton>
        </div>
      </template>
  </XtxDialog>
  <!--  -->
  <XtxDialog title="" v-model:visible="addVisible">
    <div class="address-edit">
      <div class="xtx-form">
        <div class="xtx-form-item">
          <div class="label"></div>
          <div class="field">
            <input v-model="formData.receiver" class="input" placeholder="" />
          </div>
        </div>
        <div class="xtx-form-item">
          <div class="label"></div>
          <div class="field">
            <input v-model="formData.contact" class="input" placeholder="" />
          </div>
        </div>
        <div class="xtx-form-item">
          <div class="label"></div>
          <div class="field">
            <XtxCity placeholder=""  @change="changeCty"/>
          </div>
        </div>
        <div class="xtx-form-item">
          <div class="label"></div>
          <div class="field">
           <input  v-model="formData.address" class="input" placeholder="" />
          </div>
        </div>
        <div class="xtx-form-item">
          <div class="label"></div>
          <div class="field">
            <input  v-model="formData.postalCode" class="input" placeholder="" />
          </div>
        </div>
        <div class="xtx-form-item">
          <div class="label"></div>
          <div class="field">
            <input v-model="formData.addressTags" class="input" placeholder="" />
          </div>
        </div>
      </div>
    </div>
    <template v-slot:footer>
      <XtxButton type="gray" style="margin-right: 20px" @click="addVisible=false"></XtxButton>
      <XtxButton type="primary" @click="submitAdd"></XtxButton>
    </template>
  </XtxDialog>
</template>
<script>
import { findCheckoutInfo, addAddress, createOrder } from '@/api/order'
import { onMounted, ref, reactive } from 'vue'
import { useRouter } from 'vue-router'
import { useStore } from 'vuex'
import { findCartList } from '@/api/cart'
import Message from '@/components/Message'
export default {
  name: 'XtxPayCheckoutPage',
  setup () {
    const orderInfo = ref({})
    const address = ref({})// 
    async function getOrderInfo () { // 
      const res = await findCheckoutInfo()
      orderInfo.value = res.result
      address.value = res.result.userAddresses[0]
    }
    onMounted(() => {
      getOrderInfo()
    })
    // 
    const changeVisible = ref(false)// 
    const curAddress = ref({}) // 
    function changeAddress (item) { // 
      curAddress.value = item
    }
    function submitChange () { // 
      address.value = curAddress.value
      changeVisible.value = false
    }

    // 
    const addVisible = ref(false)
    // 
    const formData = reactive({
      receiver: '',
      contact: '',
      provinceCode: '',
      cityCode: '',
      countyCode: '',
      address: '',
      postalCode: '',
      addressTags: '',
      isDefault: 0,
      fullLocation: ''
    })
    // 
    const changeCty = (data) => {
      formData.provinceCode = data.provinceCode
      formData.cityCode = data.cityCode
      formData.countyCode = data.countyCode
      formData.fullLocation = data.fullLocation
    }
    // 
    async function submitAdd () {
      const res = await addAddress(formData)
      console.log(res)
      Message({ type: 'success', text: '' })
      // 
      addVisible.value = false
      // 
      getOrderInfo()
    }

    // 
    const router = useRouter()
    const store = useStore()
    const reqParams = reactive({
      deliveryTimeType: 1,
      payType: 1,
      buyerMessage: '',
      addressId: null, // id
      goods: [] // {skuId, count}
    })
    async function submitOrder () {
      reqParams.addressId = address.value.id // id
      reqParams.goods = orderInfo.value.goods.map(item => {
        return {
          skuId: item.skuId,
          count: item.count
        }
      })
      const res = await createOrder(reqParams)
      console.log(res) // res.result.id  id
      // idurl
      router.push({
        path: '/pay',
        query: {
          orderId: res.result.id
        }
      })
      //  
      findCartList().then(res => {
        store.commit('cart/changeList', res.result)
      })
      // 
      Message({ type: 'success', text: '' })
    }
    return {
      orderInfo,
      address,
      curAddress,
      changeVisible,
      changeAddress,
      submitChange,
      addVisible,
      formData,
      changeCty,
      submitAdd,
      submitOrder
    }
  }
}
</script>
<style scoped lang="less">
.xtx-pay-checkout-page {
  .wrapper {
    background: #fff;
    padding: 0 20px;
    .box-title {
      font-size: 16px;
      font-weight: normal;
      padding-left: 10px;
      line-height: 70px;
      border-bottom: 1px solid #f5f5f5;
    }
    .box-body {
      padding: 20px 0;
    }
  }
}
.address {
  border: 1px solid #f5f5f5;
  display: flex;
  align-items: center;
  .text {
    flex: 1;
    min-height: 90px;
    display: flex;
    align-items: center;
    .none {
      line-height: 90px;
      color: #999;
      text-align: center;
      width: 100%;
    }
    > ul {
      flex: 1;
      padding: 20px;
      li {
        line-height: 30px;
        span {
          color: #999;
          margin-right: 5px;
          > i {
            width: 0.5em;
            display: inline-block;
          }
        }
      }
    }
    > a {
      color: @xtxColor;
      width: 160px;
      text-align: center;
      height: 90px;
      line-height: 90px;
      border-right: 1px solid #f5f5f5;
    }
  }
  .action {
    width: 420px;
    text-align: center;
    .btn {
      width: 140px;
      height: 46px;
      line-height: 44px;
      font-size: 14px;
      &:first-child {
        margin-right: 10px;
      }
    }
  }
}
.goods {
  width: 100%;
  border-collapse: collapse;
  border-spacing: 0;
  .info {
    display: flex;
    text-align: left;
    img {
      width: 70px;
      height: 70px;
      margin-right: 20px;
    }
    .right {
      line-height: 24px;
      p {
        &:last-child {
          color: #999;
        }
      }
    }
  }
  tr {
    th {
      background: #f5f5f5;
      font-weight: normal;
    }
    td,th {
      text-align: center;
      padding: 20px;
      border-bottom: 1px solid #f5f5f5;
      &:first-child {
        border-left: 1px solid #f5f5f5;
      }
      &:last-child {
        border-right: 1px solid #f5f5f5;
      }
    }
  }
}
.my-btn {
  width: 228px;
  height: 50px;
  border: 1px solid #e4e4e4;
  text-align: center;
  line-height: 48px;
  margin-right: 25px;
  color: #666666;
  display: inline-block;
  &.active,&:hover {
    border-color: @xtxColor;
  }
}
.total {
  dl {
    display: flex;
    justify-content: flex-end;
    line-height: 50px;
    dt {
      i {
        display: inline-block;
        width: 2em;
      }
    }
    dd {
      width: 240px;
      text-align: right;
      padding-right: 70px;
      &.price {
        font-size: 20px;
        color: @priceColor;
      }
    }
  }
}
.submit {
  text-align: right;
  padding: 60px;
  border-top: 1px solid #f5f5f5;
}
.xtx-dialog {
  .addressWrapper {
    max-height: 500px;
    overflow-y: auto;
  }
  .text {
    flex: 1;
    min-height: 90px;
    display: flex;
    align-items: center;
    &.item {
      border: 1px solid #f5f5f5;
      margin-bottom: 10px;
      cursor: pointer;
      &.active,
      &:hover {
        border-color: @xtxColor;
        background: lighten(@xtxColor, 50%);
      }
      > ul {
        padding: 10px;
        font-size: 14px;
        line-height: 30px;
      }
    }
  }
}
</style>
