export default {
  // account
  account (value) {
    if (!value) return ''
    return true
  },
  password (value) {
    if (!value) return ''
    if (!/^\w{6,24}$/.test(value)) return '6-24'
    return true
  },
  mobile (value) {
    if (!value) return ''
    if (!/^1[3-9]\d{9}$/.test(value)) return ''
    return true
  },
  code (value) {
    if (!value) return ''
    if (!/^\d{6}$/.test(value)) return '6'
    return true
  },
  isAgree (value) {
    if (!value) return ''
    return true
  }
}
