<template>
  <div class="xtx-pay-page">
    <div class="container" v-if="orderInfo.payMoney">
      <XtxBread>
        <XtxBreadItem to="/"></XtxBreadItem>
        <XtxBreadItem to="/cart"></XtxBreadItem>
        <XtxBreadItem></XtxBreadItem>
      </XtxBread>
      <!--  -->
      <div class="pay-result">
        <span class="iconfont icon-queren2 green" v-if="$route.query.payResult === 'true'"></span>
        <span class="iconfont icon-shanchu red" v-else></span>
        <p class="tit"></p>
        <p class="tip"></p>
        <p><span>{{payTypes[orderInfo.payType]}}</span></p>
        <p><span>{{orderInfo.payMoney.toFixed(2)}}</span></p>
        <div class="btn">
          <XtxButton type="primary" style="margin-right:20px"></XtxButton>
          <XtxButton type="gray"></XtxButton>
        </div>
        <p class="alert">
          <span class="iconfont icon-tip"></span>
          
        </p>
      </div>
    </div>
  </div>
</template>
<script>
import { findOrderDetail } from '@/api/pay'
import { onMounted, ref } from 'vue'
import { useRoute } from 'vue-router'
export default {
  name: 'XtxPayResultPage',
  setup () {
    const orderInfo = ref({})
    const route = useRoute()
    async function getInfo () {
      const res = await findOrderDetail(route.query.orderId)
      orderInfo.value = res.result
    }
    onMounted(() => {
      getInfo()
    })

    const payTypes = {
      1: '',
      2: ''
    }
    return {
      orderInfo,
      payTypes
    }
  }
}
</script>
<style scoped lang="less">
.pay-result {
  padding: 100px 0;
  background: #fff;
  text-align: center;
  > .iconfont {
    font-size: 100px;
  }
  .green {
    color: #1dc779;
  }
  .red {
    color: @priceColor;
  }
  .tit {
    font-size: 24px;
  }
  .tip {
    color: #999;
  }
  p {
    line-height: 40px;
    font-size: 16px;
  }
  .btn {
    margin-top: 50px;
  }
  .alert {
    font-size: 12px;
    color: #999;
    margin-top: 50px;
  }
}
</style>
