<template>
  <LoginHeader></LoginHeader>
  <section class="container">
    <nav class="tab">
      <a
        href="javascript:;"
        @click="isBindMobile = true"
        :class="{ active: isBindMobile === true }"
      >
        <i class="iconfont icon-bind" />
        <span></span>
      </a>
      <a
        href="javascript:;"
        @click="isBindMobile = false"
        :class="{ active: isBindMobile === false }"
      >
        <i class="iconfont icon-edit" />
        <span></span>
      </a>
    </nav>
    <!--  -->
    <CallbackBind v-if="isBindMobile" :union-id="unionId" />
    <!--  -->
    <CallbackPatch v-else :union-id="unionId" />
  </section>
  <LoginFooter />
</template>

<script>
import LoginHeader from './components/login-header'
import LoginFooter from './components/login-footer'
import CallbackBind from './callback-bind'
import CallbackPatch from './callback-patch'
import { ref } from 'vue'
import { userQQLogin } from '@/api/user'
import QC from 'qc'
import { useStore } from 'vuex'
import { useRouter } from 'vue-router'
export default {
  name: 'PageCallback',
  components: { LoginHeader, LoginFooter, CallbackBind, CallbackPatch },
  setup () {
    //   v-if 
    const isBindMobile = ref(true) // 
    // qq
    /**
     * 1.qq openId
     * 2.openId  
     *      
     */
    // qq  true
    console.log(QC.Login.check())
    const unionId = ref('')
    const store = useStore()
    const router = useRouter()
    if (QC.Login.check()) {
      QC.Login.getMe((openId) => {
        unionId.value = openId
        // openId
        // 
        //  openId  
        // 
        console.log('openId', openId)
        userQQLogin(openId).then((res) => {
          console.log(':', res)
          // 1. vuex
          store.commit('user/setUser', res.result)
          // 2. 
          router.push('/')
        })
      })
    }
    return {
      isBindMobile,
      unionId
    }
  }
}
</script>

<style scoped lang='less'>
.container {
  padding: 25px 0;
}
.tab {
  background: #fff;
  height: 80px;
  padding-top: 40px;
  font-size: 18px;
  text-align: center;
  a {
    color: #666;
    display: inline-block;
    width: 350px;
    line-height: 40px;
    border-bottom: 2px solid #e4e4e4;
    i {
      font-size: 22px;
      vertical-align: middle;
    }
    span {
      vertical-align: middle;
      margin-left: 4px;
    }
    &.active {
      color: @xtxColor;
      border-color: @xtxColor;
    }
  }
}
.tab-content {
  min-height: 600px;
  background: #fff;
}
</style>
